import React, { useState, useRef } from "react";
import { useCookies } from "react-cookie";
import Axios from "axios";
import lodash from "lodash";

import DefRaid from "./Definitions/Raid";
import DefGrade from "./Definitions/Grade";
// import DefStuff from "./Definitions/Stuff";

import SelectClasse from "./listDeroulante/SelecteClasse";
import SelectSpec from "./listDeroulante/Spec";

import SeachLoots from "./components/SeachLoots";

function MyCharacter() {
  const [cookies, setCookie, removeCookie] = useCookies();
  const [myCharacter, setMyCharacter] = useState(false);
  const [defWishlist, setDefWishlist] = useState(false);

  const [selectPiece, setSelectPiece] = useState(false);

  const SeachLootsRef = useRef();
  const InputName = useRef();

  if (!defWishlist) {
    setDefWishlist(lodash.groupBy(DefRaid.content, "phase"));
  }

  if (!myCharacter) {
    Axios.get(`/api/user/id/${cookies.user}`).then(({ data }) => {
      setMyCharacter(data);
    });
    return false;
  } else {
    // console.log(myCharacter.wishlist)
    const CurrentStuff = myCharacter
      ? myCharacter.stuff.map((piece, index) => {
          return piece.item !== null ? (
            <a
              href="#"
              data-wowhead={`item=${piece.item}&domain=fr`}
              key={piece.item}
            ></a>
          ) : (
            <div className={piece.type} key={piece.type + index}></div>
          );
        })
      : false;
      console.log(Object.keys(myCharacter.wishlist))
    const WLPhases = Object.keys(myCharacter.wishlist).map(wlPhase => {
      return myCharacter.wishlist[wlPhase].map((slot, index) => {
        if (slot.item === null) {
          return (
            <div
              className={slot.type}
              key={slot.type + index + wlPhase}
              onClick={ev => {
                console.log(wlPhase)
                setSelectPiece(
                  JSON.stringify({ type: slot.type, phase: wlPhase })
                );
                SeachLootsRef.current.style.top = `${ev.clientY - 20}px`;
                SeachLootsRef.current.style.left = `${ev.clientX - 100}px`;
                InputName.current.focus();
              }}
            ></div>
          );
        } else {
          return (
            <a
              href="#"
              data-wowhead={`item=${slot.item}&domain=fr`}
              key={slot.type + index + wlPhase}
            ></a>
          );
        }
      });
    });
    return (
      <div id="MyCharacter" className={myCharacter.classe}>
        <SeachLoots
          SeachLootsRef={SeachLootsRef}
          selectPiece={selectPiece}
          setMyCharacter={setMyCharacter}
          InputName={InputName}
        />
        <div className="profil">
          <div className="name">
            <div className={`iconClass ${myCharacter.classe}`}></div>
            <div className="text">{myCharacter.name}</div>
          </div>
          <div className="edit">
            <div className="selectClasse">
              <SelectClasse user={myCharacter} callback={setMyCharacter} />
            </div>
            <div className="SelectSpec">
              <SelectSpec user={myCharacter} callback={setMyCharacter} />
            </div>
            <div className="grade">{DefGrade.findName(myCharacter.grade)}</div>
          </div>
        </div>
        <h2>Equipement Actuel</h2>
        <div className="stuff">{CurrentStuff}</div>

        <h2>WishList</h2>
        <div className="wishlist">
          {defWishlist
            ? Object.keys(defWishlist).map((phase, index) => {
                return (
                  <div className={`phase ${phase}`} key={phase}>
                    <div className="title">
                      {defWishlist[phase].map(x => x.tag).join(" / ")}
                    </div>
                    <div className="stuff">{WLPhases[phase]}</div>
                  </div>
                );
              })
            : false}
        </div>
      </div>
    );
  }
}

export default MyCharacter;



