import React, { useState } from "react";
import UserInRaid from "./components/UserInRaid";
import { BrowserRouter as Router, useParams } from "react-router-dom";
import Axios from "axios";
import { useCookies } from "react-cookie";

// draggable
// onDragStart={(ev, id) => {
//   console.log("onDragStart");
// }}

// onDrop={() => {
//   console.log("onDrop");
// }}
// onDragEnd={() => {
//   console.log("onDragEnd");
// }}

function Raid() {
  const [cookies, setCookie, removeCookie] = useCookies();
  let { raidId } = useParams();
  const [raid, setRaid] = useState(false);
  let [hoverGroup, setHoverGroup] = useState(false);

  console.log(hoverGroup);

  function moveUser(user, id) {
    console.log(id, hoverGroup);
    let indexGrp = raid.roster.valid.findIndex(grp => {
      return grp.id == id;
    });

    let indexAccept = raid.roster.accept.findIndex(data => {
      return data.user == user;
    });
    deleteInValid(raid, user);
    raid.roster.accept[indexAccept].valid = Date.now();
    raid.roster.valid[indexGrp].list.push(user);

    Axios.post("/api/raid/update", { raid }).then(({ data }) => {
      setRaid(raid);
    });
    setRaid(raid);
  }

  function deleteInValid(raid, user, type = "accept") {
    let indexAccept = raid.roster[type].findIndex(data => {
      return data.user == user;
    });
    if (indexAccept !== undefined) {
      let indexGroup = raid.roster.valid
        .map(data => {
          return data.list.indexOf(user) >= 0 ? data.id : false;
        })
        .filter(x => x)
        .pop();
      // .join("");
      // console.log(indexGroup)
      if (indexGroup !== undefined) {
        let indexList = raid.roster.valid[indexGroup - 1].list.findIndex(
          x => x == user
        );
        if (indexList !== undefined) {
          delete raid.roster.valid[indexGroup - 1].list[indexList];

          let newList = raid.roster.valid[indexGroup - 1].list.filter(x => x);
          raid.roster.valid[indexGroup - 1].list = newList;
          raid.roster[type][indexAccept].valid = false;

          Axios.post("/api/raid/update", { raid }).then(({ data }) => {
            setRaid(raid);
          });
        }
      }
    }
  }

  if (!raid) {
    Axios.get(`/api/raid/${raidId}`).then(({ data }) => {
      if (!data.roster.valid) {
        console.log("OMG PAS DE VALID");
        data.roster.valid = [
          { name: "GRP1", list: [], id: 1 },
          { name: "GRP2", list: [], id: 2 },
          { name: "GRP3", list: [], id: 3 },
          { name: "GRP4", list: [], id: 4 },
          { name: "GRP5", list: [], id: 5 },
          { name: "GRP6", list: [], id: 6 },
          { name: "GRP7", list: [], id: 7 },
          { name: "GRP8", list: [], id: 8 }
        ];
      }
      setRaid(data);
    });
    return <div></div>;
  } else {
    return (
      <div id="raidComposer">
        <h2>
          {raid.name} du {raid.date.d}/{raid.date.m + 1}/{raid.date.y}{" "}
        </h2>
        <div className="OptiRaid">
          <div className="listAccept">
            <div className="title">En attente</div>
            <div
              className="list"
              onDragEnter={() => {
                setHoverGroup(false);
              }}
              onDragOver={() => {
                setHoverGroup(false);
              }}
            >
              {raid.roster.accept.map(({ user, date, valid }) => {
                // console.log(valid);
                if (!valid) {
                  return (
                    <div
                      draggable={cookies.grade == "9" ? true : false}
                      key={user}
                      onDragEnd={ev => {
                        moveUser(user, hoverGroup);
                        setHoverGroup(false);
                      }}
                    >
                      <UserInRaid
                        raidId={raidId}
                        userId={user}
                        // date={date}
                        key={user}
                        typeRoster={"accept"}
                        callback={() => {
                          deleteInValid(raid, user, "accept");
                          setRaid(false);
                        }}
                      />
                    </div>
                  );
                }
              })}
            </div>
          </div>
          <div className="raidComposer">
            {raid.roster.valid.map(grp => {
              return (
                <div className={`grp ${grp.id}`} key={`grp_${grp.id}`}>
                  <div className="nameGrp">{grp.name}</div>
                  <div
                    className="list"
                    onDragEnter={() => {
                      setHoverGroup(grp.id);
                    }}
                    onDragOver={() => {
                      setHoverGroup(grp.id);
                    }}
                  >
                    {grp.list.map(user => {
                      // console.log(user);
                      return (
                        <div
                          draggable={cookies.grade == "9" ? true : false}
                          key={user}
                          onDragEnd={ev => {
                            moveUser(user, hoverGroup);
                            setHoverGroup(false);
                          }}
                        >
                          <UserInRaid
                            raidId={raidId}
                            userId={user}
                            // date={date}
                            key={user}
                            typeRoster={"accept"}
                            callback={() => {
                              deleteInValid(raid, user, "accept");
                              setRaid(false);
                            }}
                          />
                        </div>
                      );
                    })}
                  </div>
                </div>
              );
            })}
          </div>

          <div className="refuse">
            <div className="title">Absent : {raid.roster.refuse.length}</div>
            <div className="list">
              {raid
                ? raid.roster.refuse.map(({ user, date }) => {
                    return (
                      <UserInRaid
                        raidId={raidId}
                        userId={user}
                        date={date}
                        key={user}
                        typeRoster={"refuse"}
                        callback={() => {
                          // deleteInValid(raid, user);
                          setRaid(false);
                        }}
                      />
                    );
                  })
                : false}
            </div>
          </div>
          <div className="bench">
            <div className="title">Bench {raid.roster.bench.length}</div>
            <div className="list">
              {raid
                ? raid.roster.bench.map(({ user, date }) => {
                    // console.log(user, date);
                    return (
                      <UserInRaid
                        raidId={raidId}
                        userId={user}
                        date={date}
                        key={user}
                        typeRoster={"bench"}
                        callback={() => {
                          // deleteInValid(raid, user);
                          setRaid(false);
                        }}
                      />
                    );
                  })
                : false}
            </div>
          </div>
        </div>
      </div>
    );
    // }
  }
}

export default Raid;



