import React, { useRef } from "react";
import Axios from "axios";

const Grades = [
  { name: "Officer", lvl: 9 },
  { name: "Vétéran", lvl: 8 },
  { name: "Roster", lvl: 7 },
  { name: "Membre", lvl: 6 },
  { name: "Recrue", lvl: 5 },
  { name: "Remplacant", lvl: 4 },
  { name: "Invité", lvl: 0 }
];

function Grade({ user, callback }) {
  //   console.log(user.grade);
  const SelectGrade = useRef();
  return (
    <select
      name="SelectGrade"
      ref={SelectGrade}
      defaultValue={user.grade == null ? 0 : parseInt(user.grade)}
      onChange={() => {
        user.grade = SelectGrade.current.value;
        Axios.post("/api/user/update/", { user }).then(({ data }) => {
          //   console.log(data);
          if (data) {
            callback(false);
          }
        });
      }}
    >
      {Grades.map(grade => {
        return (
          <option value={grade.lvl} key={grade.lvl}>
            {grade.name}
          </option>
        );
      })}
    </select>
  );
}
export default Grade;



