import React, { useState } from "react";
import { useCookies } from "react-cookie";
import Axios from "axios";
import lodash from "lodash";

import SelectClasse from "./listDeroulante/SelecteClasse";
import SelectSpec from "./listDeroulante/Spec";
import SelectGrade from "./listDeroulante/Grade";
// import Grades from "./listDeroulante/Grade";

const Grades = [
  { name: "Officer", lvl: 9 },
  { name: "Vétéran", lvl: 8 },
  { name: "Roster", lvl: 7 },
  { name: "Membre", lvl: 6 },
  { name: "Recrue", lvl: 5 },
  { name: "Remplacant", lvl: 4 },
  { name: "Invité", lvl: 0 },
  { name: "NULL", lvl: "null" }
];

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
// import { isNumber } from "util";

function Admin() {
  const [cookies, setCookie, removeCookie] = useCookies();
  const [Users, setUsers] = useState(false);
  const [groupBy, setGroupBy] = useState("classe");

  if (Users == false) {
    Axios.get("/api/users").then(({ data }) => {
      setUsers(lodash.groupBy(data, groupBy));
    });
  }
  if (cookies.grade >= 4) {
    return (
      <div id="Roster">
        <h3>Roster</h3>
        <div className="orderBy">
          <ul>
            <li
              className={groupBy === "classe" ? "active" : ""}
              onClick={() => {
                setGroupBy("classe");
                setUsers(false);
              }}
            >
              Classe
            </li>
            <li
              className={groupBy === "spe" ? "active" : ""}
              onClick={() => {
                setGroupBy("spe");
                setUsers(false);
              }}
            >
              Spé
            </li>
            <li
              className={groupBy === "grade" ? "active" : ""}
              onClick={() => {
                setGroupBy("grade");
                setUsers(false);
              }}
            >
              Grade
            </li>
          </ul>
        </div>

        <div className="roster">
          {Users
            ? Object.keys(Users).map(spec => {
                return spec !== "null" ? (
                  <div key={spec}>
                    <div className="type">
                      {groupBy === "grade"
                        ? Grades.find(x => {
                            return x.lvl == spec;
                          }).name
                        : spec}
                    </div>
                    <div className="list">
                      {Users[spec].map(user => {
                        if (user.grade > 1) {
                          return (
                            <div className="user" key={user.id}>
                              <div
                                className={`classe iconClass ${user.classe}`}
                              ></div>

                              {/* {groupBy !== "spe" ? (
                              <div
                                className={`spe iconSpe ${user.spe}`}
                              ></div>
                            ) : (
                              false
                            )} */}
                              <div className={`spe iconSpe ${user.spe}`}></div>
                              <div className={`name colorClass ${user.classe}`}>
                                {user.name}
                              </div>
                            </div>
                          );
                        } else {
                          return false;
                        }
                      })}
                    </div>
                  </div>
                ) : (
                  false
                );
              })
            : false}
        </div>
      </div>
    );
  } else {
    return <div>Petit Malin va :D</div>;
  }
}

export default Admin;



