import React, { useState, useRef } from "react";
import axios from "axios";
import { useCookies } from "react-cookie";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck, faTimes, faPlus } from "@fortawesome/free-solid-svg-icons";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import ListHeure from "../listDeroulante/Heure";
import ListRaid from "../listDeroulante/Raid";

const indexStatusRaidForUser = (raid, user) => {
  let indexAccept = raid.roster.accept
    .map((currentUser, index) => {
      return currentUser.user === user ? index : false;
    })
    .filter(x => x !== false)
    .join();
  let indexRefuse = raid.roster.refuse
    .map((currentUser, index) => {
      return currentUser.user === user ? index : false;
    })
    .filter(x => x !== false)
    .join();

  return { indexAccept, indexRefuse };
};

function Day({ day }) {
  const [cookies] = useCookies();
  const [event, setEvent] = useState(false);
  const [modalNew, setModalNew] = useState(false);
  const [classStatus, setClassStatus] = useState(false);
  const raid = useRef();
  const refHours = useRef();
  const refMin = useRef();
  const refRaid = useRef();
  const refName = useRef();
  const refDesc = useRef();

  let dayRaid = new Date(day).getDate();
  let monthRaid = new Date(day).getMonth();
  let yearRaid = new Date(day).getFullYear();

  let url = `/api/raid/${new Date(day).getDate()}/${new Date(
    day
  ).getMonth()}/${new Date(day).getFullYear()}`;
  axios.get(url).then(({ data }) => {
    if (data && event === false) {
      setEvent(data);
    }
  });
  if (event !== false && classStatus === false) {
    const { indexAccept, indexRefuse } = indexStatusRaidForUser(
      event,
      cookies.user
    );
    if (indexAccept !== "") {
      setClassStatus("accept");
    }
    if (indexRefuse !== "") {
      setClassStatus("refuse");
    }
  }
  return (
    <div className={classNameDay(day)} key={new Date(day).getTime()}>
      <div className="number">{new Date(day).getDate()}</div>
      {cookies.grade == 9 &&
      classNameDay(day).lastIndexOf("passed") < 0 &&
      event == false ? (
        <div className="addRaid">
          <div
            onClick={() => {
              setModalNew(!modalNew);
            }}
          >
            <FontAwesomeIcon icon={faPlus} />
          </div>

          <div className={modalNew ? "newRaid show" : "newRaid hidden"}>
            <div
              className="close"
              onClick={() => {
                setModalNew(!modalNew);
              }}
            >
              <FontAwesomeIcon icon={faTimes} />
            </div>
            <div className="date">
              Le {`${dayRaid}/${monthRaid}/${yearRaid}`}
            </div>
            <div className="hours">
              <ListHeure refHours={refHours} refMin={refMin} />
            </div>
            <div className="raid">
              <ListRaid refRaid={refRaid} />
            </div>
            <div className="name">
              <input type="text" name="name" ref={refName} />
            </div>
            <div className="desc">
              <textarea name="desc" ref={refDesc}></textarea>
            </div>
            <div
              className="btn"
              onClick={() => {
                let hoursInput = refHours.current.value;
                let minInput = refMin.current.value;
                let raidInput = refRaid.current.value;
                let nameInput = refName.current.value;
                let descInput = refDesc.current.value;
                if (nameInput === "") {
                  nameInput = raidInput.toUpperCase();
                }
                let timestamp =
                  new Date(
                    `${yearRaid}-${monthRaid}-${dayRaid}T${hoursInput}:${minInput}:00`
                  ).getTime() / 1000;
                axios
                  .post("/api/raid/new", {
                    type: raidInput,
                    name: nameInput,
                    desc: descInput,
                    date: {
                      d: dayRaid,
                      m: monthRaid,
                      y: yearRaid,
                      hours: hoursInput,
                      min: minInput,
                      timestamp: timestamp
                    },
                    roster: {
                      refuse: [],
                      accept: [],
                      bench: [],
                      valid: [
                        { name: "GRP1", list: [] },
                        { name: "GRP2", list: [] },
                        { name: "GRP3", list: [] },
                        { name: "GRP4", list: [] },
                        { name: "GRP5", list: [] },
                        { name: "GRP6", list: [] },
                        { name: "GRP7", list: [] },
                        { name: "GRP8", list: [] }
                      ]
                    }
                  })
                  .then(({ data }) => {
                    if (data) {
                      window.location.reload();
                    }
                  });
              }}
            >
              Valider
            </div>
          </div>
        </div>
      ) : (
        false
      )}
      <div className="events">
        {event ? (
          <div className={`raid ${classStatus}`} ref={raid}>
            <Link to={`/roster/${event.id}`}>
              <div className={`icon ${event.type}`}>
                <div className="stat">
                  <div className="accept">{event.roster.accept.length}</div>
                  <div className="refuse">{event.roster.refuse.length}</div>
                  {/* <div className="bench">{event.roster.bench.length}</div> */}
                </div>
              </div>
            </Link>
            <div className="info">
              <div className="name">{event.name}</div>
              <div className="hours">
                {event.date.hours}h{event.date.min}
              </div>
              <div className="tools">
                <div
                  className="refuse"
                  onClick={() => {
                    axios
                      .post("/api/raid/refuse", {
                        id: event.id,
                        user: cookies.user
                      })
                      .then(({ data }) => {
                        if (data) {
                          setClassStatus("refuse");
                        }
                      });
                  }}
                >
                  <FontAwesomeIcon icon={faTimes} />
                </div>
                <div
                  className="accept"
                  onClick={() => {
                    axios
                      .post("/api/raid/accept", {
                        id: event.id,
                        user: cookies.user
                      })
                      .then(({ data }) => {
                        if (data) {
                          setClassStatus("accept");
                        }
                      });
                  }}
                >
                  <FontAwesomeIcon icon={faCheck} />
                </div>
              </div>
            </div>
          </div>
        ) : (
          false
        )}
      </div>
    </div>
  );
}
function classNameDay(date) {
  let result = ["day"];
  let dayRaid = new Date(date).getDate();
  let monthRaid = new Date(date).getMonth();
  let yearRaid = new Date(date).getFullYear();

  let dayNow = new Date(Date.now()).getDate();
  let monthNow = new Date(Date.now()).getMonth();
  let yearNow = new Date(Date.now()).getFullYear();

  if (dayRaid === dayNow && monthRaid === monthNow && yearRaid === yearNow) {
    result.push("today");
  } else if (new Date(date).getTime() < Date.now()) {
    result.push("passed");
  }

  return result.join(" ");
}
export default Day;



