import React, { useState } from "react";
import Axios from "axios";
import { useCookies } from "react-cookie";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCheck,
  faTimes,
  faToiletPaper
} from "@fortawesome/free-solid-svg-icons";

function UserInRaid({ userId, raidId, typeRoster, callback  }) {
  const [cookies, setCookie, removeCookie] = useCookies();

  const [user, setUer] = useState(false);
  if (!user) {
    Axios.get(`/api/user/id/${userId}`).then(({ data }) => {
      setUer(data);
    });
    return <div></div>;
  } else {
    return (
      <div className="UserInRaid">
        <div
          className={`iconClass ${user.classe}`}
          style={{ height: "20px", width: "20px" }}
        ></div>
        <div
          className={`iconSpe ${user.spe}`}
          // style={{ height: "20px", width: "20px" }}
        ></div>
        <div className={`name colorClass ${user.classe}`}>{user.name}</div>
        {cookies.grade == 9 ? (
          <div className="tools">
            {typeRoster !== "accept" ? (
              <div
                className="accept"
                onClick={() => {
                  Axios.post("/api/raid/accept", {
                    id: raidId,
                    user: userId
                  }).then(()=>{
                    callback()
                  });
                }}
              >
                <FontAwesomeIcon icon={faCheck} />
              </div>
            ) : (
              false
            )}
            {typeRoster !== "refuse" ? (
              <div
                className="refuse"
                onClick={() => {
                  Axios.post("/api/raid/refuse", {
                    id: raidId,
                    user: userId
                  }).then(()=>{
                    callback()
                  });
                }}
              >
                <FontAwesomeIcon icon={faTimes} />
              </div>
            ) : (
              false
            )}
            {typeRoster !== "bench" ? (
              <div
                className="bench"
                onClick={() => {
                  Axios.post("/api/raid/bench", {
                    id: raidId,
                    user: userId
                  }).then(()=>{
                    callback()
                  });
                }}
              >
                <FontAwesomeIcon icon={faToiletPaper} />
              </div>
            ) : (
              false
            )}
          </div>
        ) : (
          false
        )}
      </div>
    );
  }
}

export default UserInRaid;



