import React, { useRef } from "react";
import Axios from "axios";

const Specs = [
  { name: "Heal", value: "heal" },
  { name: "Tank", value: "tank" },
  { name: "DPS", value: "dps" },
  { name: "", value: null }
];

function SelectSpec({ user, callback }) {
  //   console.log(user.grade);
  const SelectSpec = useRef();
  return (
    <select
      name="SelectSpec"
      ref={SelectSpec}
      defaultValue={user.spe == null ? "" : user.spe}
      onChange={() => {
        user.spe = SelectSpec.current.value;
        Axios.post("/api/user/update/", { user }).then(({ data }) => {
          //   console.log(data);
          if (data) {
            callback(false);
          }
        });
      }}
    >
      {Specs.map(spec => {
        return (
          <option value={spec.value} key={spec.value}>
            {spec.name}
          </option>
        );
      })}
    </select>
  );
}
export default SelectSpec;



