import React, { useState } from "react";
import { useCookies } from "react-cookie";
import Axios from "axios";
import lodash from "lodash";

import SelectClasse from "./listDeroulante/SelecteClasse";
import SelectSpec from "./listDeroulante/Spec";
import SelectGrade from "./listDeroulante/Grade";

import Grade from "./Definitions/Grade";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";

function Admin() {
  const [cookies, setCookie, removeCookie] = useCookies();
  const [Users, setUsers] = useState(false);
  const [groupBy, setGroupBy] = useState("grade");

  if (Users == false) {
    Axios.get("/api/users").then(({ data }) => {
      setUsers(lodash.groupBy(data, groupBy));
    });
  }
  if (cookies.grade == 9) {
    return (
      <div id="Admin">
        <h3>Roster</h3>
        <div className="orderBy">
          <ul>
            <li
              className={groupBy === "classe" ? "active" : false}
              onClick={() => {
                setGroupBy("classe");
                setUsers(false);
              }}
            >
              Classe
            </li>
            <li
              className={groupBy === "spe" ? "active" : false}
              onClick={() => {
                setGroupBy("spe");
                setUsers(false);
              }}
            >
              Spé
            </li>
            <li
              className={groupBy === "grade" ? "active" : false}
              onClick={() => {
                setGroupBy("grade");
                setUsers(false);
              }}
            >
              Grade
            </li>
          </ul>
        </div>

        <div className="roster">
          {Users
            ? Object.keys(Users).map(spec => {
                return (
                  <div key={spec}>
                    <div className="type">
                      {Users[spec].length}{" "}
                      {groupBy === "grade"
                        ? Grade.content.find(x => {
                            return x.lvl == spec;
                          }).name
                        : spec}
                    </div>
                    {Users[spec].map(user => {
                      return (
                        <div className="user" key={user.id}>
                          <div
                            className={`classe iconClass ${user.classe}`}
                          ></div>
                          <div className={`spe iconSpe ${user.spe}`}></div>
                          <div className={`name colorClass ${user.classe}`}>
                            {user.name}
                          </div>
                          <div className="SelectClasse">
                            <SelectClasse user={user} callback={setUsers} />
                          </div>
                          <div className="SelectGrade">
                            <SelectGrade user={user} callback={setUsers} />
                          </div>
                          <div className="SelectSpec">
                            <SelectSpec user={user} callback={setUsers} />
                          </div>
                          <div className="countAccept">0</div>
                          <div className="countRefuse">0</div>
                          <div className="countBench">0</div>
                          <div
                            className="delete"
                            onClick={() => {
                              Axios.get(`/api/user/delete/${user.id}`).then(
                                ({ data }) => {
                                  if (data) {
                                    setUsers(false);
                                  }
                                }
                              );
                            }}
                          >
                            <FontAwesomeIcon icon={faTrash} />
                          </div>
                        </div>
                      );
                    })}
                  </div>
                );
              })
            : false}
        </div>
      </div>
    );
  } else {
    return <div>Petit Malin va :D</div>;
  }
}

export default Admin;



