import React, { useRef } from "react";
import Axios from "axios";

const Classes = {
  druid: {
    color: "#FF7D0A",
    name: "Druide"
  },
  hunter: {
    color: "#ABD473",
    name: "Chasseur"
  },
  mage: {
    color: "#69CCF0",
    name: "Mage"
  },
  paladin: {
    color: "#F58CBA",
    name: "Paladin"
  },
  priest: {
    color: "#FFFFFF",
    name: "Pretre"
  },
  rogue: {
    color: "#FFF569",
    name: "Voleur"
  },
  shaman: {
    color: "#0070DE",
    name: "Chaman"
  },
  warlock: {
    color: "#9482C9",
    name: "Démoniste"
  },
  warrior: {
    color: "#C79C6E",
    name: "Guerrier"
  },
  "":{
    color: "#FFF",
    name: ""
  }
};

function SelectClasse({ user, callback }) {
  const selectClasse = useRef();
  return (
    <div className="selectClasse">
      <select
        name="selectClasse"
        ref={selectClasse}
        defaultValue={user.classe == null ? "" : user.classe}
        className={`colorClass ${user.classe}`}
        onChange={() => {
          user.classe = selectClasse.current.value;
          Axios.post("/api/user/update/", { user }).then(({ data }) => {
            if (data) {
                callback(false);
            }
          });
        }}
      >
        {Object.keys(Classes).map(index => {
          return (
            <option value={index} key={index} className={`colorClass ${index}`}>
              {Classes[index].name}
            </option>
          );
        })}
      </select>
    </div>
  );
}

export default SelectClasse;



