import React from "react";
import "./stylesheets/main.css";
import { useCookies } from "react-cookie";
import axios from "axios";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Calendar from "./calendar";
import Login from "./Login";
import MyCharacter from "./MyCharacter";
import Raid from "./Raid";
import Admin from "./Admin";
import Roster from "./Roster";
import WishList from "./WishList";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPowerOff } from "@fortawesome/free-solid-svg-icons";

function App() {
  const [cookies, setCookie, removeCookie] = useCookies();

  if (cookies.token) {
    // axios.post("/auth/verif", { token: cookies.token }).then(({ data }) => {
    //   if (!data) {
    //     removeCookie("token");
    //     // window.location.reload();
    //   }
    // });
    return (
      <Router>
        <div className="App">
          <div id="nav">
            <ul>
              <li>
                <Link to="/calendar">Calendrier</Link>
              </li>
              <li><Link to="/guilde">Roster</Link></li>

              <li>
                <Link to="/mycharater">Mon Personnage</Link>
              </li>
              <li><Link to="/wishlist">WishList</Link></li>
              {cookies.grade == 9 ? (
                <li>
                  <Link to="/admin">Admin</Link>
                </li>
              ) : (
                false
              )}
            </ul>
            <div
              className="profil"
              onClick={() => {
                console.log("DECO");
                removeCookie("token");
                // window.location.reload();
              }}
            >
              <FontAwesomeIcon icon={faPowerOff} />
            </div>
          </div>
          <div id="main">
            <Switch>
              <Route path="/calendar">
                <Calendar />
              </Route>
              <Route path="/mycharater">
                <MyCharacter />
              </Route>
              <Route exact={true} path="/">
                Bienvenue les Loulous le site est en construction mais vous
                pouvez déjà y participé.
              </Route>
              <Route path="/roster/:raidId">
                <Raid />
              </Route>
              <Route path="/guilde">
                <Roster />
              </Route>
              <Route path="/wishlist">
                <WishList />
              </Route>
              <Route path="/admin">
                <Admin />
              </Route>
            </Switch>
          </div>
        </div>
      </Router>
    );
  } else {
    return <Login />;
  }
}

export default App;



