import React, { useState } from "react";
import Axios from "axios";

import DefRaid from "./Definitions/Raid";

function WishList() {
  console.log(DefRaid.content);
  const [Users, setUsers] = useState(false);
  const [listLoots, setListLoots] = useState(false);

  const [currentBoss, setCurrentBoss] = useState(false);

  if (!listLoots) {
    Axios.get("/api/loots").then(({ data }) => {
      setListLoots(data);
    });
    return <div>WISHLIST</div>;
  } else {
    console.log(listLoots);
    console.log(
      listLoots.filter(item => {
        return item.boss.indexOf("lucifron") >= 0;
      })
    );

    return (
      <div id="WishList">
        {/* {Users.map(user => {
          if(user.wishlist.phase1){
            return <div>{user.name}</div>;
          }else{
            return false
          }
        })} */}
        {DefRaid.content.map(raid => {
          console.log(raid);
          return (
            <div className="raid">
              <div className="name">{raid.name}</div>
              <div className="listBoss">
                {raid.boss.map(boss => {
                  return (
                    <div
                      className={currentBoss == boss ? "boss active" : "boss"}
                      onClick={() => {
                        setCurrentBoss(boss);
                      }}
                    >
                      {boss}
                    </div>
                  );
                })}
              </div>
            </div>
          );
        })}
        <div className="listLoots">
          {listLoots
            .filter(item => {
              return item.boss.indexOf(currentBoss) >= 0;
            })
            .map(item => {
              return <div className="item">
                <div className="name">
                <a
              href="#"
              data-wowhead={`item=${item.id}&domain=fr`}
              key={item.id}
            >{item.name}</a>
                </div>
                <div className="listUserWL">
                  {/* <div className="needer">Hystérias</div>
                  <div className="needer">Hystérias</div>
                  <div className="needer">Hystérias</div>
                  <div className="needer">Hystérias</div>
                  <div className="needer">Hystérias</div>
                  <div className="needer">Hystérias</div>
                  <div className="needer">Hystérias</div> */}

                </div>
                
            </div>;
            })}
        </div>
      </div>
    );
  }
}

export default WishList;



