import React, { useState, useRef } from "react";
import Axios from "axios";
import { useCookies } from "react-cookie";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCheck,
  faTimes,
  faToiletPaper
} from "@fortawesome/free-solid-svg-icons";
// const wowhead = require('wowhead');

// console.log(wowhead)

function SearchLoots({ SeachLootsRef, selectPiece, setMyCharacter, InputName }) {
  const [cookies, setCookie, removeCookie] = useCookies();
  
  const resultSearchDiv = useRef();
  const [resultSearch, setResultSearch] = useState(false);
  return (
    <div
      id="SeachLoots"
      ref={SeachLootsRef}
      onMouseLeave={() => {
        SeachLootsRef.current.style.top = `-1000px`;
        SeachLootsRef.current.style.left = `-1000px`;
        InputName.current.value = "";
        setResultSearch(false);
        // resultSearchDiv.current.innerHTML = "";
      }}
    >
      {/* {selectPiece} */}
      <input
        type="text"
        ref={InputName}
        placeholder={"Nom du loot"}
        onChange={() => {
          //   console.log(InputName.current.value);
          if (InputName.current.value.length > 1) {
            Axios.get(`/api/loots/name/${InputName.current.value.toLowerCase()}`).then(
              ({ data }) => {
                //   console.log(data);
                setResultSearch(data);
              }
            );
          }
        }}
      />
      {resultSearch ? (
        <div id="resultSearch" ref={resultSearchDiv}>
          {resultSearch.slice(0, 12).map(loot => {
            return (
              <a
                href="#"
                data-wowhead={`item=${loot.id}&domain=fr`}
                key={`resultSearch${loot.id}`}
                onClick={ev => {
                  ev.preventDefault();
                  console.log(loot.id, selectPiece, cookies.user);
                  Axios.post("/api/user/updateWL", {
                    idUser: cookies.user,
                    type: JSON.parse(selectPiece),
                    item: loot.id
                  }).then(({ data }) => {
                    console.log(data);
                    document.location.reload(true);
                    // setMyCharacter(data)
                    // window.reload()
                  });
                }}
              >
                {loot.name}
              </a>
            );
          })}
        </div>
      ) : (
        false
      )}
    </div>
  );
}

export default SearchLoots;



