const content = [
  { name: "Officier", lvl: 9 },
  { name: "Vétéran", lvl: 8 },
  { name: "Roster", lvl: 7 },
  { name: "Membre", lvl: 6 },
  { name: "Recrue", lvl: 5 },
  { name: "Remplacant", lvl: 4 },
  { name: "Invité", lvl: 0 },
  { name: "NULL", lvl: "null" }
];
const findName = lvl => {
  return content.find(grade => {
    return grade.lvl == lvl;
  }).name;
};
export default { content, findName };



