import React, { useState } from "react";
function ListRaid({refRaid}) {
  const defRaid = [
    { type: "mc", name: "Coeur de Magma" },
    { type: "ony", name: "Onyxia" }
  ];
  return (
    <div className="ListRaid">
      <select name="raid" ref={refRaid}>
        {defRaid.map(raid => {
          return (
            <option
              value={raid.type}
              key={raid.type}
            >
              {raid.name}
            </option>
          );
        })}
      </select>
    </div>
  );
}

export default ListRaid;



